<template>
  <div class="login" style="margin-top: 25vh; width: 100%;">
    <el-card class="box-card">
      <div slot="header" class="clearfix" style="text-align: center;">
        <span>登录界面</span>
      </div>
      <el-form ref="form" :model="loginInfo" :rules="rules">
        <el-form-item label="账户" prop="username">
          <el-input v-model="loginInfo.username"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input type="password" v-model="loginInfo.password"></el-input>
        </el-form-item>
        <el-form-item style="text-align: center;">
          <el-button type="primary" @click="onSubmit">登录</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import Cookie from 'js-cookie'
import { getMenu, selectAdmin } from '@/api';
import Mock from 'mockjs';
export default {
  data() {
    return {
      loginInfo: {
        username: "",
        password: "",
      },
      rules:{
        username: [
        { required: true, message: '请输入账户', trigger: 'blur' },
        ],
        password:[
          { required: true, message: '请输入密码', trigger: 'blur' }
        ]
      }
    }
  },
  methods:{
    onSubmit(){

      this.$refs.form.validate((valid) => {
        if(valid){
          selectAdmin(this.loginInfo).then((res) =>{
            var list = res.data.data
            console.log("管理员信息list", list)
            if(list.length !== 0){
            
              Cookie.set('token', Mock.Random.guid())
             
              var userinfo = {}
              userinfo.name = list[0].name
              // userinfo.img = "https://i.pinimg.com/736x/cb/c4/ad/cbc4ada33aabb32d27203e62f5ac7525.jpg"
              userinfo.img = list[0].url
              userinfo.access = "超级管理员"
             
              // 登录页面跳转前保存用户信息到state 
              this.$store.commit('updatedUserInfo', userinfo)
              
              this.$router.push('/home')
            }
            else{
              this.$message.error("账户不存在");
            }
          })
          
        }
        else{
          this.$message.warning("请认真填写信息哦~")
        }
      
      })

    }
  }
}
</script>


<style scoped>
.text {
  font-size: 14px;
}

.item {
  margin-bottom: 18px;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}

.clearfix:after {
  clear: both
}

.box-card {
  width: 480px;
}

.el-card{
  margin: 0 auto;
  width: 25vw;
}
</style>