<template>
  <div>
        <el-menu :default-active="$route.path === '/home' ? '/' : $route.path" class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose"
          :collapse="isCollapse" background-color="#545c64" text-color="#fff" active-text-color="#ffd04b" >
          <h3>{{ isCollapse ? '后台' : "后台管理系统" }}</h3>

          <el-menu-item @click="clickMenu(item)" class="el-menu-item__content" v-for="item in noChildren" :key="item.name" :index="item.path">
            <i :class="`el-icon-${item.icon}`"></i>
            <span slot="title">{{ item.label }}</span>
          </el-menu-item>


          <el-submenu @click="clickMenu(item)" class="el-menu-item__content" v-for="item in hasChildren" :key="item.label" :index="item.label">
            <template slot="title">
              <i :class="`el-icon-${item.icon}`"></i>
              <span slot="title">{{ item.label }}</span>
            </template>
            <el-menu-item-group v-for="kid in item.children" :key="kid.name">
              <el-menu-item :index="kid.path" :class="`el-icon-${kid.icon}`" @click="clickMenu(kid)">{{ kid.label }}</el-menu-item>
            </el-menu-item-group>
          </el-submenu>
        </el-menu>

  </div>
</template>

<script>

export default {
  data() {
    return {

    };
  },
  methods: {
    clickMenu(item){
      // 防止重复push同一个路径报错


      if(this.$route.path === item.path || (this.$route.path == '/home' && item.path == '/')) return;
      
      this.$router.push(item.path)

      console.log("当前路由 \n" + this.$route.path)

      this.$store.dispatch('selectMenu', item)
    },
  
  },
  computed: {
    noChildren() {
      return this.menuData.filter(item => !item.children)
    },
    hasChildren() {
      return this.menuData.filter(item => item.children)
    },
    isCollapse(){
      return this.$store.state.tab.isCollapse
    },
    menuData(){
     return [
            {
              path: '/home',
              name: 'home',
              label: '首页',
              icon: 's-home',
              url: 'Home.vue'
            },
            {
              path: '/doctor',
              name: 'doctor',
              label: '医生管理',
              icon: 'user',
              url: 'Doctor.vue'
            },
            {
              path: '/department',
              name: 'department',
              label: '科室管理',
              icon: 'first-aid-kit',
              url: 'Department.vue'
            },

            {
              path: '/user',
              name: 'user',
              label: '患者管理',
              icon: 'female',
              url: 'User.vue'
            },
            {
              path: '/announcement',
              name: 'announcement',
              label: '公告管理',
              icon: 'postcard',
              url: 'Announcement.vue'
            },
            {
              path: '/schedule',
              name: 'schedule',
              label: '排班管理',
              icon: 'date',
              url: 'Schedule.vue'
            },
            
          ]

    }
  },
  mounted(){
    // console.log(this.$route)
  }
}
</script>

<style lang="less" scoped>

.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 200px;
  min-height: 400px;
}


.el-menu {
  border-right: none;
  line-height: 0;
  height: 100vh;

  h3 {
    color: #fff;
    text-align: center;
    line-height: 48px;
    font-size: 16px;
    font-weight: normal;
  }
}

.el-menu-item__content {
  text-align: left;
}
</style>