<template>
    <div class="announcement">
      <el-dialog :title="dialogType == 1 ? '修改信息' : '添加信息'" :visible.sync="dialogVisible" width="45%" :before-close="clearData">
        <el-form ref="form" :model="form" :inline="true" label-width="90px" label-position="right" size="medium">
          <el-form-item label="编号:">
            <el-input v-model="form.announcement_id" :placeholder="dialogType == 1 ? form.announcement_id : '请输入编号'" :disabled="dialogType == 1"></el-input>
          </el-form-item>
          <el-form-item label="标题:">
            <el-input v-model="form.title" placeholder="请输入标题"></el-input>
          </el-form-item>
          <el-form-item label="内容:">
            <el-input v-model="form.content" placeholder="请输入内容" type="textarea"></el-input>
          </el-form-item>
          <el-form-item label="时间:" style="margin-left: 10px;">
         
            <el-date-picker type="date" placeholder="选择日期" v-model="form.creation_time" style="max-width: 80%;"
              value-format="yyyy-MM-dd" 
              ></el-date-picker>

        </el-form-item>
          <el-form-item label="创建人:">
            <el-input v-model="form.creator" placeholder="请输入创建人"></el-input>
          </el-form-item>
        </el-form>
  
        <span slot="footer" class="dialog-footer">
          <el-button @click="clearData">取消</el-button>
          <el-button type="primary" @click="submit">确定</el-button>
        </span>
      </el-dialog>
  
      <div class="announcement-header" style="margin-bottom: 10px; display: flex; justify-content: space-between; align-items: center;">
        <div>
          <el-button type="primary" @click="showDialog">+ 新增</el-button>
          <el-button type="warning" @click="exportExcel"> 导出</el-button>
        </div>
  
        <el-form :model="searchForm" :inline="true" style="margin-top: 15px">
          <el-form-item>
            <el-input v-model="searchForm.announcement_id" placeholder="输入公告编号"></el-input>
          </el-form-item>
          <el-form-item>
            <el-input v-model="searchForm.title" placeholder="输入公告标题"></el-input>
          </el-form-item>
          <el-form-item>
            <el-input v-model="searchForm.creator" placeholder="输入创建人"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="handleSearch">搜索</el-button>
            </el-form-item>
        </el-form>
      </div>
  
      <div class="table-list" style="height: 80vh; background: #fff;">
        <el-table :data="pageTicket" style="width: 100%" height="85%" id="announcement_list">
          <el-table-column prop="announcement_id" label="编号" min-width="100"></el-table-column>
          <el-table-column prop="title" label="标题" min-width="150"></el-table-column>
          <el-table-column prop="content" label="内容" min-width="200"></el-table-column>
          <el-table-column prop="creation_time" label="创建时间" min-width="150" ></el-table-column>
          <el-table-column prop="creator" label="创建人" min-width="100"></el-table-column>
          <el-table-column label="操作" min-width="180">
            <template slot-scope="scope">
              <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
              <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
        <div style="background: #fff; float: right;">
          <el-pagination
            @size-change="handleSizeChange" 
            @current-change="handleCurrentChange" 
            :current-page="currentPage"  
            :page-sizes="[5, 10, 20]"  
            :page-size="pagesize"  
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
          </el-pagination>
        </div>
      </div>
    </div>
  </template>
  
  <script>
  import { getAnnouncementList, delAnnouncement, updateAnnouncement, insertAnnouncement, selectAnnouncement } from "@/api/index"
  import FileSaver from 'file-saver';
  import XLSX from "xlsx"
  import changeTime from "@/utils/changeTime";

  export default {
    data() {
      return {
        announcementList: [], // 总数据
        dialogType: 0, // 0 - 新增 1 - 编辑
        dialogVisible: false,
        form: {
          announcement_id: '',
          title: '',
          content: '',
          creation_time: '',
          creator: ''
        },
        searchForm: {
          announcement_id: '',
          title: '',
          creator:'',
        },
        total: 0,
        currentPage: 1,
        pagesize: 10,
        pageTicket: [] // 当前页面数据
      };
    },
    methods: {
      // 分页操作
      getPageInfo() {
        this.pageTicket = [];
        for (let i = (this.currentPage - 1) * this.pagesize; i < this.total; i++) {
          this.pageTicket.push(this.announcementList[i]);
          if (this.pageTicket.length === this.pagesize) break;
        }
      },
      handleSizeChange(size) {
        this.pagesize = size;
        this.getPageInfo();
      },
      handleCurrentChange(pagenum) {
        this.currentPage = pagenum;
        this.getPageInfo();
      },
      showDialog() {
        this.dialogType = 0;
        this.dialogVisible = !this.dialogVisible;
        this.form = {};
      },
      clearData() {
        this.dialogVisible = false;
        this.$refs.form.resetFields();
      },
      submit() {
        this.$refs.form.validate((value) => {
          if (value) {
            if (this.dialogType == 0) {
              this.add();
            } else {
              this.edit();
            }
            this.clearData();
          } else {
            alert("数据添加有误,请重新填写");
          }
        });
      },
      initList() {
        getAnnouncementList().then(
          (res) => {
            this.announcementList = res.data.data;

            // 对时间格式进行转化
            this.announcementList.forEach( (obj) => {
                obj.creation_time = changeTime(obj.creation_time)
            } )

            this.total = this.announcementList.length;
            // console.log("list", this.announcementList)
            this.getPageInfo();
          },
          (err) => {
            console.log("err", err);
          }
        );
      },
      add() {
        insertAnnouncement(this.form).then(
          (res) => {
            this.initList();
          },
          (err) => {
            console.log("err", err);
          }
        );
      },
      edit() {
        updateAnnouncement(this.form).then(
          (res) => {
            if (res.status == 0) {
              this.initList();
            }
          },
          (err) => {
            console.log("err", err);
          }
        );
      },
      handleEdit(index, row) {
        this.dialogType = 1;
        this.dialogVisible = !this.dialogVisible;
        this.form = row;
      },
      handleDelete(index, row) {
        this.$confirm('此操作将永久删除该公告数据, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
          this.delete(row)
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });

    
      },
      delete(row){
        delAnnouncement(row).then(
          (res) => {
            this.initList()
          },
          (err) => {
            console.log("err", err);
          }
        );
      },

      handleSearch() {
        selectAnnouncement(this.searchForm).then(
          (res) => {
            this.announcementList = res.data.data;
            // console.log(this.announcementList, "ancList")

             // 对时间格式进行转化
             this.announcementList.forEach( (obj) => {
                obj.creation_time = changeTime(obj.creation_time)
            } )


            this.total = this.announcementList.length;
            this.getPageInfo();
        
          },
          (err) => {
            console.log("err", err);
          }
        );
      },
      exportExcel() {
        var wb = XLSX.utils.book_new();
        var ws = XLSX.utils.json_to_sheet(this.announcementList);
        XLSX.utils.book_append_sheet(wb, ws, "Sheet1");
        var wbout = XLSX.write(wb, {
          bookType: "xlsx",
          bookSST: true,
          type: "array"
        });
        try {
          FileSaver.saveAs(
            new Blob([wbout], { type: "application/octet-stream" }),
            "公告信息.xlsx"
          );
        } catch (e) {
          if (typeof console !== "undefined") console.log(e, wbout);
        }
        return wbout;
      }
    },
    mounted() {
      this.initList();
    }
  };
  </script>
  
  <style scoped>
  .table-list {
    background: #fff;
  }
  </style>
  