// 前端接口


import http from "../utils/request"

export const getData = () => {
    return http.get('/home/getData')
}

export const getUser = (params) => {
    return http.get('/user/getUser', params)
}

export const addUser = (data) => {
    return http.post('/user/add', data)
}

export const delUser = (data) => {
    return http.post('/user/del', data)
}

export const editUser = (data) => {
    return http.post('/user/edit', data)
}

export const getMenu = (data) => {
    return http.post('/permission/getMenu', data)
}

export const getImages = () => {
    return http.get('/mall/getimages')
}


export const getAdminList = () => {
    return http.get("/admin/list")
}


/**
 * 医生
 * @returns 
 */

// 获取医生列表
export const getDocList = () =>{
    return http.get("/doctor/list")
}


// 删除医生列表
export const delDoc = (data)=>{
    // console.log("发送删除请求")
    // console.log(data)
    return http.get(`/doctor/delete?doctor_id=${data.doctor_id}`)
}

// 编辑用户
export const updateDoc = (data) => {
    console.log("修改用户请求")
    console.log(data)
    return http.post('/doctor/update', data)
}


// 新增医生
export const insertDoc = (data) => {
    console.log("新增医生")
    console.log(data)
    return http.post('/doctor/insert', data)
}

// 查询医生
export const selectDoc = (data) =>{
    // console.log("查询医生")
    // console.log(data)
    return http.get("/doctor/list", {params:data})
}



