<template>
    <div class="schedule">
      <el-dialog title="提示" :visible.sync="dialogVisible" width="45%" :before-close="clearData">
        <el-form ref="form" :model="form" :inline="false" label-width="90px" label-position="right" size="medium">
          <el-form-item label="排班编号:">
            <el-input v-model="form.schedule_id" :placeholder="dialogType === 1 ? form.schedule_id : '请输入编号'" :disabled="dialogType === 1"></el-input>
          </el-form-item>
          <el-form-item label="医生:">
            <!-- <el-input v-model="form.doctor_id" placeholder="请输入医生编号"></el-input> -->
            <el-select v-model="form.doctor_id" placeholder="请选择医生" style="width: 140px" size="small">
            <el-option
                    v-for="item in docList"
                    :key="item.id"
                    :label="item.name"
                    :value="item.doctor_id">
            </el-option>
          </el-select>
          </el-form-item>
          <el-form-item label="日期:">
            <el-date-picker v-model="form.date" placeholder="选择日期" type="date" value-format="yyyy-MM-dd"></el-date-picker>
          </el-form-item>
          <el-form-item label="班次:">
            <el-input v-model="form.shift_time" placeholder="请输入班次"></el-input>
          </el-form-item>
          <el-form-item label="科室:">
            <!-- <el-input v-model="form.department_id" placeholder="请输入科室编号"></el-input> -->
          <el-select v-model="form.department_id" placeholder="请选择科室" style="width: 140px" size="small">
            <el-option
                    v-for="item in dptList"
                    :key="item.department_id"
                    :label="item.department_name"
                    :value="item.department_id">
            </el-option>
          </el-select>
          </el-form-item>
          <el-form-item label="是否空闲:" >

            <el-select v-model="form.is_available" placeholder="" style="width: 140px" size="small">
              <el-option
                      label="空闲"
                      value="1">
              </el-option>
              <el-option
                      label="在忙"
                      value="0">
              </el-option>
          </el-select>

          </el-form-item>
          <el-form-item label="接诊人数:">
            <el-input v-model="form.visit_count" placeholder="请输入接诊人数"></el-input>
          </el-form-item>
          <el-form-item label="备注:">
            <el-input v-model="form.remarks" type="textarea" placeholder="请输入备注"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="clearData">取消</el-button>
          <el-button type="primary" @click="submit">确定</el-button>
        </span>
      </el-dialog>
  
      <div class="schedule-header" style="margin-bottom: 10px; display: flex; justify-content: space-between; align-items: center;">
        <div>
          <el-button type="primary" @click="showDialog">新增</el-button>
          <el-button type="warning" @click="exportExcel">导出</el-button>
        </div>
        <el-form :model="searchForm" :inline="true" style="margin-top: 15px">
          <el-form-item>
            <el-input v-model="searchForm.schedule_id" placeholder="输入排班编号"></el-input>
            
          </el-form-item>
          <el-form-item>
            <el-input v-model="searchForm.doctor_id" placeholder="输入医生编号"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="handleSearch">搜索</el-button>
          </el-form-item>
        </el-form>
      </div>
  
      <div class="table-list" style="height: 80vh; background: #fff;">
        <el-table :data="pageTicket" style="width: 100%" height="85%" id="schedule_list">
          <el-table-column prop="schedule_id" label="排班编号" min-width="100"></el-table-column>
          <el-table-column prop="name" label="医生姓名" min-width="100"></el-table-column>
          <el-table-column prop="date" label="日期" min-width="150"></el-table-column>
          <el-table-column prop="shift_time" label="班次" min-width="100"></el-table-column>
          <el-table-column prop="department_name" label="科室名称" min-width="100"></el-table-column>
          <el-table-column label="是否空闲" min-width="100">
            <template slot-scope="scope">
              <span>{{ scope.row.is_available === 1 ? '空闲' : '忙碌' }}</span>
            </template>
          </el-table-column>
          <el-table-column prop="visit_count" label="接诊人数" min-width="100"></el-table-column>
          <el-table-column prop="remarks" label="备注" min-width="200"></el-table-column>
          <el-table-column label="操作" min-width="180">
            <template slot-scope="scope">
              <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
              <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
        <div style="background: #fff; float: right;">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="[5, 10, 20]"
            :page-size="pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
          </el-pagination>
        </div>
      </div>
    </div>
  </template>
  
  <script>
  import { getScheduleList, delSchedule, updateSchedule, insertSchedule, selectSchedule, getDocList, getDeptList } from "@/api/index";
  import FileSaver from 'file-saver';
  import XLSX from "xlsx";
  
  export default {
    data() {
      return {
        docList: [],
        dptList: [],
        scheduleList: [],
        dialogType: 0, // 0 - 新增  1 - 编辑
        dialogVisible: false,
        form: {
          schedule_id: "",
          doctor_id: "",
          date: "",
          shift_time: "",
          department_id: "",
          is_available: "",
          visit_count: "",
          remarks: "",
          name:"",
          department_name:'',
        },
        searchForm: {
          schedule_id: "",
          doctor_id: ""
        },
        currentPage: 1,
        pageSize: 10,
        total: 0,
        pageTicket: []
      };
    },
    created() {
      this.getData();
    },
    mounted(){
      getDocList().then((res) => {
        this.docList = res.data.data
      })
      getDeptList().then((res) => {
        this.dptList = res.data.data
        console.log("dptList:", this.dptList)
      }).catch((err)=>{
        console.log("获取失败dplist err", err)
      })
    },
    methods: {
      getData() {
        getScheduleList().then((res) => {
            this.scheduleList = res.data.data;
            this.total = this.scheduleList === undefined ? 0 : this.scheduleList.length ;
            console.log("当前数据", this.scheduleList)
            this.pageTicket = this.scheduleList.slice((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize);
        });
      },
      showDialog() {
        this.dialogType = 0;
        this.dialogVisible = true;
        this.form = {}
      },
      clearData() {
        this.dialogVisible = false;
        this.$refs.form.resetFields();
      },
      handleEdit(index, row) {
        this.dialogType = 1;
        this.dialogVisible = true;
        this.form = { ...row };
      },
      handleDelete(index, row) {
        this.$confirm('此操作将永久删除该公告数据, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
          this.delete(row)
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      delete(row){
        delSchedule(row).then(() => {
            this.getData()
        })
      },
      submit() {
        if (this.dialogType === 0) {
          insertSchedule(this.form).then((res) => {
              this.getData();
              this.dialogVisible = false;
          });
        } else {
          updateSchedule(this.form).then((res) => {
              this.getData();
              this.dialogVisible = false;
          });
        }
      },
      handleSearch() {
        selectSchedule(this.searchForm).then((res) => {
            this.scheduleList = res.data.data;
            this.total = this.scheduleList.length;
            this.pageTicket = this.scheduleList.slice((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize);
        });
      },
      handleSizeChange(val) {
        this.pageSize = val;
        this.pageTicket = this.scheduleList.slice((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize);
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        this.pageTicket = this.scheduleList.slice((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize);
      },
      exportExcel() {
        const data = this.scheduleList.map((item) => ({
          排班编号: item.schedule_id,
          医生编号: item.doctor_id,
          日期: item.date,
          班次: item.shift_time,
          科室编号: item.department_id,
          是否可用: item.is_available ? "是" : "否",
          接诊人数: item.visit_count,
          备注: item.remarks
        }));
        const ws = XLSX.utils.json_to_sheet(data);
        const wb = XLSX.utils.book_new();
        XLSX.utils.book_append_sheet(wb, ws, "排班数据");
        const wbout = XLSX.write(wb, { bookType: "xlsx", type: "array" });
        try {
          FileSaver.saveAs(
            new Blob([wbout], { type: "application/octet-stream" }),
            "排班数据.xlsx"
          );
        } catch (e) {
          console.log(e, wbout);
        }
      }
    }
  };
  </script>
  
  <style scoped>
  .schedule {
    padding: 20px;
  }
  </style>
  