<template>
  <div class="DOC">

    <!-- <button @click="handleClick()">点击获取列表</button> -->
    <!-- 弹窗 -->
    <el-dialog title="提示" :visible.sync="dialogVisible" width="45%" :before-close="clearData">


      <!-- 表单信息 -->
      <el-form ref="form" :model="form" :inline="true" label-width="90px" label-position="right" size="medium">
        <el-form-item label="编号:">
          <el-input v-model="form.doctor_id" :placeholder="diglogType == 1 ? form.doctor_id : '请输入编号'" :disabled="diglogType == 1"></el-input>
        </el-form-item>
        <el-form-item label="工号:">
          <el-input v-model="form.job_number" :placeholder="diglogType == 1 ?  form.job_number : '请输入工号'" :disabled="diglogType == 1"></el-input>
        </el-form-item>
        <el-form-item label="密码:">
          <el-input v-model="form.password" placeholder="请输入密码"></el-input>
        </el-form-item>
        
        <el-form-item label="职称:">
          <el-select v-model="form.professional_title_id" placeholder="请选择职称" style="width: 140px" size="small">
            <el-option
                    v-for="item in titlesList"
                    :key="item.id"
                    :label="item.title_name"
                    :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        
        


        <el-form-item label="姓名:">
          <el-input v-model="form.name" placeholder="请输入姓名"></el-input>
        </el-form-item>
        <el-form-item label="电话:">
          <el-input v-model="form.phone" placeholder="请输入电话号码"></el-input>
        </el-form-item>
        <el-form-item label="邮箱:">
          <el-input v-model="form.email" placeholder="请输入邮箱"></el-input>
        </el-form-item>
        <el-form-item label="简介:">
          <el-input v-model="form.introduction" placeholder="请输入简介" type="textarea" resize="none"></el-input>
        </el-form-item>
        <el-form-item label="挂号费:">
          <el-input v-model="form.registration_fee" placeholder="请输入挂号费"></el-input>
        </el-form-item>
        <!-- <el-form-item label="密码:" prop="password">
          <el-select v-model="form.password" placeholder="请输入密码">
            <el-option label="男" :value="1"></el-option>
            <el-option label="女" :value="0"></el-option>
          </el-select>
        </el-form-item> -->
        <el-form-item label="入职日期：">
          <el-col :span="11" style="width: 80%;">
            <el-date-picker type="date" placeholder="选择日期" v-model="form.entry_date" style="width: 100%;"
              value-format="yyyy-MM-dd"></el-date-picker>

              <!-- <el-date-picker
      v-model="value1"
      type="datetime"
      placeholder="选择日期时间">
    </el-date-picker>  -->
          </el-col>
        </el-form-item>
        <!-- <el-form-item label="地址:" prop="addr">
          <el-input v-model="form.addr" placeholder="请输入地址"></el-input>
        </el-form-item> -->
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="clearData">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </span>
    </el-dialog>

    <!-- 增加 - 导出 - 搜索 -->
    <div class="users-header"
      style="margin-bottom: 10px; display: flex; justify-content: space-between; align-items: center;">
      <div>
        <el-button type="primary" @click="showDialog">+ 新增</el-button>
        <el-button type="warning" @click="exportExcel"> 导出</el-button>
      </div>

      <el-form :model="searchForm" :inline="true" style="margin-top: 15px">
        <el-form-item>
          <el-input v-model="searchForm.doctor_id" placeholder="输入医生id"></el-input>
        </el-form-item>
        
        <el-form-item>
          <el-input v-model="searchForm.name" placeholder="输入医生姓名"></el-input>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="handleSearch">搜索</el-button>
        </el-form-item>
      </el-form>
    </div>


    <!-- 信息列表 -->
    <div class="table-list" style="height: 80vh ; background: #fff;">
      <el-table :data="pageTicket" style="width: 100%" height="85%" id="doc_list">

        <el-table-column prop="doctor_id" label="编号" width="50">
        </el-table-column>

        <el-table-column prop="job_number" label="工号" width="80">
        </el-table-column>

        <el-table-column prop="password" label="密码" width="80">
        </el-table-column>

        <el-table-column prop="name" label="姓名" min-width="80">
        </el-table-column>

        <el-table-column prop="avatar" label="图片" min-width="100">
        </el-table-column>

        <el-table-column prop="phone" label="电话号码" min-width="120">
        </el-table-column>

        <el-table-column prop="email" label="邮箱" min-width="120">
        </el-table-column>

        <el-table-column prop="introduction" label="介绍" min-width="300">
        </el-table-column>

        <el-table-column prop="registration_fee" label="挂号费" min-width="100">
        </el-table-column>

        <el-table-column prop="title_name" label="职称" min-width="100">
        </el-table-column>


        <el-table-column prop="entry_date" label="入职时间" min-width="150">
        </el-table-column>

        <el-table-column label="操作" min-width="180">
          <template slot-scope="scope">
            <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
            <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div style="background: #fff; float: right;">
        <!-- <el-pagination layout="prev, pager, next" :total="total" @current-change="handlePage" :page-size="20">
        </el-pagination> -->
        <el-pagination
          @size-change="handleSizeChange" 
          @current-change="handleCurrentChange" 
          :current-page="currentPage"  
          :page-sizes="[5, 10, 20]"  
          :page-size="pagesize"  
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">   //数据的总条数，这是后端要展示的总数据条数
        </el-pagination>

      </div>
    </div>


  </div>
</template>

<script>

import { getImages, getAdminList, selectTitle, getDocList, delDoc, updateDoc, insertDoc, selectDoc } from "@/api/index"
import FileSaver from 'file-saver';
import XLSX from "xlsx"


export default {
  data() {
    return {
      titlesList: [],
      docList: [],  // 总数据
      diglogType: 0,  // 0 - 新增  1 - 编辑
      dialogVisible: false,
      form: {
        doctor_id: '',
        job_number: '',
        password: '',
        name: '',
        avatar: '',
        email: '',
        introduction: '',
        registration_fee: '',
        entry_date: '',
        phone: '',
        department_id: '',
        professional_title_id: ''
      },
      searchForm:{
        doctor_id:'',
        name: '',
      },
      total: 0,
      currentPage: 1,
      pagesize: 10,
      pageTicket: [], //当前页面数据
    };
  },
  methods: {
    // 分页操作

    // 获取当前页数据
    getPageInfo(){
      
      // console.log("total:" + this.total)

      this.pageTicket = []

      for(let i = (this.currentPage - 1) * this.pagesize; i < this.total; i++){
        console.log("进入循环")
        this.pageTicket.push(this.docList[i])
        // 是否达到一页的数量
        if(this.pageTicket.length === this.pagesize) break;
      }
      
      console.log("当前页面数据")
      console.log(this.pageTicket)
    },

    // 修改页面大小
    handleSizeChange(size){
      this.pagesize = size
      this.getPageInfo()
    },

    // 修改页面页码-
    handleCurrentChange(pagenum){
      this.currentPage = pagenum
      this.getPageInfo()
    },


    // 新增按钮
    showDialog() {
      this.diglogType = 0
      this.dialogVisible = !this.dialogVisible
        this.form = {}
    },


    // 弹窗操作

    clearData() {
      this.dialogVisible = false
      this.$refs.form.resetFields()
    },
    submit() {
      console.log("提交的数据", this.form)
      this.$refs.form.validate((value) => {
        if (value) {
          if (this.diglogType == 0) {
            console.log("增加用户")
            this.add()
          }
          else {
            this.edit()
          }
            this.clearData()
        }
        else {
          alert("数据添加有误,请重新填写")
        }
      })

    },
    cancel() {
      this.clearData()
    },

    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    },
    handleExceed(files, fileList) {
      this.$message.warning(`当前限制选择 3 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
    },
    beforeRemove(file, fileList) {
      return this.$confirm(`确定移除 ${file.name}？`);
    },
    initList() {
      getDocList().then(
        (res) => {
          this.docList = res.data.data
        
          this.total = this.docList.length
          
          this.getPageInfo()
        }
      )
    },
    handleDelete(index, row) {
      this.$confirm('此操作将永久删除该医生数据, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$message({
          type: 'success',
          message: '删除成功!'
        });
        this.delete(row)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },
    handleEdit(index, row) {
      // 显示弹窗
      this.diglogType = 1;
      this.dialogVisible = !this.dialogVisible
      // this.form = JSON.parse(JSON.stringify(row))
      this.form = { ...row }
      console.log("编辑按钮点击:" + this.form)
    },

    // 点击搜索按钮
    handleSearch(){
      this.select(this.searchForm)
    },

    // 查询用户
    select(data){
      selectDoc(data).then((res) => {
        this.docList = res.data.data
        this.total = this.docList.length
        this.getPageInfo()
      }).catch(
        ()=>{
          console.log("请求失败")
        }
      )
    },

    // 删除用户
    delete(row) {
      // console.log(row)
      delDoc({ doctor_id: row.doctor_id }).then(
        () => {
          this.initList()
        }
      ).catch(
        console.log("请求失败")
      )
    },

    // 编辑用户
    edit() {
      updateDoc(this.form).then(
        () => {
          this.initList()
        }
      ).catch(
        () => {
        }
      )
    },

    // 新增医生
    add() {
      insertDoc(this.form).then(
        () => {
          console.log("插入数据成功")
          this.initList()
        }
      ).catch(
        () => {
          console.log("插入数据失败")
        }
      )
    },

    // 获取职称列表
    getTitleList(){
      selectTitle().then((res) => {
        this.titlesList = res.data.data
      })
    },

    // 导出表格
    exportExcel() {
      var xlsxParam = { raw: true };
      /* 从表生成工作簿对象 */
      var wb = XLSX.utils.table_to_book(
        document.querySelector("#doc_list"),
        xlsxParam
      );
      /* 获取二进制字符串作为输出 */
      var wbout = XLSX.write(wb, {
        bookType: "xlsx",
        bookSST: true,
        type: "array",
      });
      try {
        FileSaver.saveAs(
          //Blob 对象表示一个不可变、原始数据的类文件对象。
          //Blob 表示的不一定是JavaScript原生格式的数据。
          //File 接口基于Blob，继承了 blob 的功能并将其扩展使其支持用户系统上的文件。
          //返回一个新创建的 Blob 对象，其内容由参数中给定的数组串联组成。
          new Blob([wbout], { type: "application/octet-stream" }),
          //设置导出文件名称
          "医生列表.xlsx"
        );
      } catch (e) {
        if (typeof console !== "undefined") console.log(e, wbout);
      }
      return wbout;
    },

  },
  mounted() {
    this.initList()
    this.getTitleList()
  }
}
</script>

<style scoped>
.el-carousel__item:nth-child(2n) {
  background-color: #99a9bf;
}

.el-carousel__item:nth-child(2n+1) {
  background-color: #d3dce6;
}

.time {
  font-size: 13px;
  color: #999;
}

.bottom {
  margin-top: 13px;
  line-height: 12px;
}

.button {
  padding: 0;
  float: right;
}

.image {
  width: 100%;
  display: block;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}

.clearfix:after {
  clear: both
}
</style>