<template>
    <div class="Department">
  
      <!-- 弹窗 -->
      <el-dialog title="提示" :visible.sync="dialogVisible" width="45%" :before-close="clearData">
        <!-- 表单信息 -->
        <el-form ref="form" :model="form" :inline="true" label-width="90px" label-position="right" size="medium">
          <el-form-item label="编号:">
            <el-input v-model="form.department_id" :placeholder="dialogType == 1 ? form.department_id : '请输入编号'" :disabled="dialogType == 1"></el-input>
          </el-form-item>
          <el-form-item label="名称:">
            <el-input v-model="form.department_name" placeholder="请输入名称"></el-input>
          </el-form-item>
          
          <el-form-item label="上级科室:">
            <el-input v-model="form.department_pid" placeholder="请输入上级科室"></el-input>
          </el-form-item>

          <el-form-item label="科室等级:">
            <el-input v-model="form.department_level" placeholder="请输入科室等级"></el-input>
          </el-form-item>

          <el-form-item label="简介:">
            <el-input v-model="form.department_description" placeholder="请输入简介" type="textarea"></el-input>
          </el-form-item>
        </el-form>
  
        <span slot="footer" class="dialog-footer">
          <el-button @click="clearData">取 消</el-button>
          <el-button type="primary" @click="submit">确 定</el-button>
        </span>
      </el-dialog>
  
      <!-- 增加 - 导出 - 搜索 -->
      <div class="departments-header" style="margin-bottom: 10px; display: flex; justify-content: space-between; align-items: center;">
        <div>
          <el-button type="primary" @click="showDialog">+ 新增</el-button>
          <el-button type="warning" @click="exportExcel"> 导出</el-button>
        </div>
  
        <el-form :model="searchForm" :inline="true" style="margin-top: 15px">
          <el-form-item>
            <el-input v-model="searchForm.department_id" placeholder="输入科室ID"></el-input>
          </el-form-item>
          
          <el-form-item>
            <el-input v-model="searchForm.department_name" placeholder="输入科室名称"></el-input>
          </el-form-item>
  
          <el-form-item>
            <el-button type="primary" @click="handleSearch">搜索</el-button>
          </el-form-item>
        </el-form>
      </div>
  
      <!-- 信息列表 -->
      <div class="table-list" style="height: 80vh ; background: #fff;">
        <el-table :data="pageTicket" style="width: 100%" height="85%" id="dept_list" >
          <el-table-column prop="department_id" label="编号" min-width="100"></el-table-column>
          <el-table-column prop="department_name" label="名称" min-width="150"></el-table-column>
          <el-table-column label="上级科室" min-width="100">
            <template slot-scope="scope">
              <span> {{ getPName(scope.row.department_pid) }}  </span>
            </template>
          </el-table-column>
          <el-table-column prop="department_level" label="科室等级" min-width="100"></el-table-column>
          <el-table-column prop="department_path" label="科室路径" min-width="100"></el-table-column>
          <el-table-column prop="department_description" label="简介" min-width="200"></el-table-column>
          <el-table-column label="操作" width="180">
            <template slot-scope="scope">
              <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
              <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
        <div style="background: #fff; float: right;">
          <el-pagination
            @size-change="handleSizeChange" 
            @current-change="handleCurrentChange" 
            :current-page="currentPage"  
            :page-sizes="[5, 10, 20]"  
            :page-size="pagesize"  
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
          </el-pagination>
        </div>
      </div>
  
    </div>
  </template>
  
  <script>
  import { getDeptList, delDept, updateDept, insertDept, selectDept } from "@/api/index"
  import FileSaver from 'file-saver';
  import XLSX from "xlsx"
  
  export default {
    data() {
      return {
        deptList: [],  // 总数据
        dialogType: 0,  // 0 - 新增  1 - 编辑
        dialogVisible: false,
        form: {
          department_id: '',
          department_name: '',
          department_description: '',
          department_pid:'',
          department_level:'',
          department_path: "",
        },
        searchForm:{
          department_id:'',
          department_name: '',
        },
        total: 0,
        currentPage: 1,
        pagesize: 10,
        pageTicket: [], //当前页面数据
      };
    },
    methods: {
      getPName(department_pid){
        const parent = this.deptList.find(dept => dept.department_id === department_pid);
        return parent ? parent.department_name : '无';
      },  

      // 分页操作
  
      // 获取当前页数据
      getPageInfo() {
        this.pageTicket = []
  
        for (let i = (this.currentPage - 1) * this.pagesize; i < this.total; i++) {
          this.pageTicket.push(this.deptList[i])
          if (this.pageTicket.length === this.pagesize) break;
        }
      },
  
      // 修改页面大小
      handleSizeChange(size) {
        this.pagesize = size
        this.getPageInfo()
      },
  
      // 修改页面页码
      handleCurrentChange(pagenum) {
        this.currentPage = pagenum
        this.getPageInfo()
      },
  
      // 新增按钮
      showDialog() {
        this.dialogType = 0
        this.dialogVisible = !this.dialogVisible
        this.form = {}
      },
  
      // 弹窗操作
      clearData() {
        this.dialogVisible = false
        this.$refs.form.resetFields()
      },
      submit() {
        this.$refs.form.validate((value) => {
            console.log("提交的form", this.form)
          if (value) {
            if (this.dialogType == 0) {
              this.add()
            } else {
              this.edit()
            }
            this.clearData()
          } else {
            alert("数据添加有误,请重新填写")
          }
        })
      },
  
      handleDelete(index, row) {
        this.$confirm('此操作将永久删除该科室数据, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
          this.delete(row)
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      handleEdit(index, row) {
        // 显示弹窗
        this.dialogType = 1;
        this.dialogVisible = !this.dialogVisible
        this.form = { ...row }
      },
  
      // 点击搜索按钮
      handleSearch() {
        this.select(this.searchForm)
      },
  
      // 查询部门
      select(data) {
        selectDept(data).then((res) => {
          this.deptList = res.data.data
          this.total = this.deptList.length
          this.getPageInfo()
        }).catch(() => {
          console.log("请求失败")
        })
      },
  
      // 删除部门
      delete(row) {
        delDept({ department_id: row.department_id }).then(() => {
          this.initList()
        }).catch(() => {
          console.log("请求失败")
        })
      },
  
      // 编辑部门
      edit() {

        this.form.department_path = '|' + this.form.department_pid + '||' + this.form.department_id + "|"

        updateDept(this.form).then(() => {
          this.initList()
        }).catch(() => {
          console.log("请求失败")
        })
      },
  
      // 新增部门
      add() {

        this.form.department_path = '|' + this.form.department_pid + '||' + this.form.department_id + "|"

        insertDept(this.form).then(() => {
          this.initList()
        }).catch(() => {
          console.log("插入数据失败")
        })
      },
  
      // 初始化列表
      initList() {
        getDeptList().then((res) => {
          this.deptList = res.data.data
          console.log(this.deptList)
          this.total = this.deptList.length
          this.getPageInfo()
        }).catch(() => {
          console.log("请求失败")
        })
      },
  
      // 导出表格
      exportExcel() {
        var xlsxParam = { raw: true };
        var wb = XLSX.utils.table_to_book(
          document.querySelector("#dept_list"),
          xlsxParam
        );
        var wbout = XLSX.write(wb, {
          bookType: "xlsx",
          bookSST: true,
          type: "array",
        });
        try {
          FileSaver.saveAs(
            new Blob([wbout], { type: "application/octet-stream" }),
            "部门列表.xlsx"
          );
        } catch (e) {
          if (typeof console !== "undefined") console.log(e, wbout);
        }
        return wbout;
      },
  
    },

    computed:{
      getPath(){
        return '|' + this.form.department_pid + '||' + this.form.department_id + "|"
      },
      
    },  

    mounted() { 
      this.initList()
    }
  }
  </script>
  
  <style scoped>
  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }
  
  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }
  
  .time {
    font-size: 13px;
    color: #999;
  }
  
  .bottom {
    margin-top: 13px;
    line-height: 12px;
  }
  
  .button {
    padding: 0;
    float: right;
  }
  
  .image {
    width: 100%;
    display: block;
  }
  
  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  
  .clearfix:after {
    clear: both
  }
  </style>
  